  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  font-family: Nunito, sans-serif;
  font-weight: 400;
  display: flex;
  position: absolute;
}

.loader h1 {
  border: 1px solid #fff;
  border-radius: 2rem;
  margin: 5rem;
  padding: 2rem;
  font-family: inherit;
  font-size: 3rem;
  animation: 2s forwards headerAnim;
}

@keyframes headerAnim {
  0% {
    opacity: 0;
    animation-timing-function: ease-in;
    transform: scale(0);
  }

  38% {
    opacity: 1;
    animation-timing-function: ease-out;
    transform: scale(1);
  }

  55% {
    animation-timing-function: ease-in;
    transform: scale(.7);
  }

  72% {
    animation-timing-function: ease-out;
    transform: scale(1);
  }

  81% {
    animation-timing-function: ease-in;
    transform: scale(.8);
  }

  100% {
    animation-timing-function: ease-out;
    transform: scale(1);
  }
}

